.exemple {
  table-layout: auto;
  width: 100%;

  text-align: left;
  border: 1px rgb(255, 255, 255) solid;
}

.SpareSetList img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

tr {
}

td {
  background-color: rgb(255, 255, 255);

  line-height: 30px;
}

input {
  text-overflow: ellipsis;
  word-wrap: break-word;
  border: none;
  margin: 0;
  padding-top: 0px;
  background-color: rgb(245, 245, 245);
}

input:focus {
  background-color: rgb(255, 255, 255);
}
.Pwd input {
  background-color: rgb(49, 49, 49);
}

.Pwd:hover input {
  background-color: #7b241c;
  color: white;
}

.Pwd:hover input::placeholder {
  color: white;
}

.Actifa::placeholder {
  color: rgb(255, 255, 255);
}

[data-status="Actif"] {
  background-color: #252bd4;
}

[data-status="Holiday"] {
  background-color: #ec7063;
}
[data-status="Off"] {
  background-color: #3498d8;
}

.Imagee {
  box-shadow: 0px 0px 8px rgb(109, 109, 109);
}
thead {
  background-color: rgb(255, 255, 255);
  line-height: 40px;
}

input::placeholder {
  color: rgb(81, 81, 81);

  font-family: "Times New Roman", Times, serif;
}

.kkk {
  background-color: rgb(255, 255, 255);
  padding: 50px;
}

.kkk .ARow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 2px;
}

.kkk .ARowB {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 30px;
}
.kkk .ACol {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

input.Red,
.Red::placeholder {
  background-color: rgb(255, 0, 0);
  color: white;
}

input.Green,
.Green::placeholder {
  background-color: green;
  color: white;
}

input.Yellow,
.Yellow::placeholder {
  background-color: #d7d704;
  color: white;
}

input.Blue,
.Blue::placeholder {
  background-color: #3498db;
  color: white;
}

input.Pink,
.Pink::placeholder {
  background-color: #ffaebc;
  color: white;
}

input.Test,
.Turquoise::placeholder {
  background-color: #aad6a0;
  color: white;
}

input.Test,
.ColorA::placeholder {
  background-color: #A45C40;
  color: white;
}

input.Test,
.ColorB::placeholder {
  background-color: #AE388B;
  color: white;
}

.AAA{
  border-radius: 5px;
}

.ARow{
  display: flex;
  flex-direction: row;
  justify-content: center;

}

.ButAc span.material-icons{
  vertical-align: middle;
  
 
}

td{
  margin-inline: 15px;
  padding-inline: 8px;
  background-color: #ffffff;
}